<template>
  <v-card>
    <v-card-title>Comment Form</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validations>
        <v-row>
          <v-col cols="12">
            <v-text-field @keydown="onEnter" v-model="comment" label="Comment" multi-line type="text" clearable></v-text-field>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions class="text-right">
      <v-btn text @click="onCancel">Close</v-btn>
      <v-btn color="primary" :loading="isSaving" text @click="saveForm">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import ChipInput from './ChipInput.vue';
export default {
  name: 'comment-form',
  components: { ChipInput },
  props: ['onSave', 'currentComment', 'onCancel'],
  data() {
    return {
      isSaving: false,
      comment: [],
      valid: false,
    };
  },
  mounted() {
    if (this.currentComment) {
      this.comment = this.currentComment;
    }
  },
  methods: {
    onEnter(event) {
      if (event && event.keyCode == 13) {
        event.preventDefault();
        this.saveForm();
      }
    },
    saveForm() {
      this.isSaving = true;
      this.onSave(this.comment)
        .then(() => {
          this.$toasted.show('Successfully saved comment');
          this.onCancel();
        })
        .catch(err => {
          this.$toasted.show('Unsuccessful to save comment');
        })
        .finally(() => {
          this.isSaving = false;
        });
    },
  },
};
</script>
