<template>
  <v-combobox outlined :value="_value" @change="onInput" @update:search-input="onChipInputChange" chips clearable :label="label" multiple solo>
    <template v-slot:selection="{ attrs, item, select, selected }">
      <v-chip v-bind="attrs" :input-value="selected" close @click="select" @click:close="remove(item)">
        <strong>{{ item }}</strong
        >&nbsp;
      </v-chip>
    </template>
  </v-combobox>
</template>
<script>
export default {
  name: 'chip-input',
  props: ['label', 'value', 'onChipInputChange'],
  computed: {
    _value: {
      get() {
        return this.value;
      },
      set(value) {
        return value;
      },
    },
  },
  methods: {
    onInput(v) {
      this.$emit('input', v);
    },
    remove(item) {
      const value = this._value.filter(val => val !== item);
      this.$emit('input', value);
    }
  },
};
</script>
