<template>
  <div>
    <grid-toolbar
      toolbarTitle="Slave Devices"
      :file-name="fileName"
      :items="selectedItems"
      :format-data-service="formatDataService"
    >
      <template v-slot:gridCounter>
        <tooltip-icon-button
          btnColor="default"
          btnIcon="fa-refresh"
          btnTooltip="Refresh system apps"
          buttonSize="medium"
          :onClick="() => refreshDevices()"
        ></tooltip-icon-button>
        <header-selector :grid-config-change="gridConfigChange" :fieldKeys="itemKeys" :tableName="tableName" />
        <grid-items-counter :num="selectedItems.length" />
      </template>
      <template v-slot:customAction>
        <v-btn
          x-small
          class="mr-2 btn-height-22 white--text"
          color="nubeYellow"
          @click="syncSlaveDevices"
          :loading="isSyncActive"
        >
          Sync<v-icon x-small right>fa-refresh</v-icon>
        </v-btn>
      </template>
      <template v-slot:search>
        <v-menu bottom left>
          <template v-slot:activator="{ on, attrs }">
            <v-btn dark icon v-bind="attrs" v-on="on">
              <v-icon :color="appliedFilterCount > 0 ? 'blue' : 'white'">fa-filter</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-subheader>Filters</v-subheader>
            <v-list-item>
              <v-list-item-title>
                <v-checkbox class="mt-0" hide-details v-model="filters.isOnline" label="Online Devices" />
              </v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title>
                <v-checkbox class="mt-0" hide-details v-model="filters.isOffline" label="Offline Devices" />
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
        <v-text-field
          v-model="search"
          @input="onSearchChange"
          append-icon="fa-search"
          label="Search"
          single-line
          hide-details
          class="pl-10"
        />
        <p class="count-wrapper">
          <span class="count-container">
            <span class="count-circle lightGrey" />
            <span class="grey--text">Total</span>
            <span class="number">{{ totalCount }}</span>
          </span>
          <span class="count-container">
            <span class="count-circle nubeGreen" />
            <span class="grey--text">Online</span>
            <span class="number">{{ onlineCount }}</span>
          </span>
          <span class="count-container">
            <span class="count-circle nubeYellow" />
            <span class="grey--text">Offline</span>
            <span class="number">{{ offlineCount }}</span>
          </span>
        </p>
      </template>
      <template v-slot:actions>
        <v-btn
          x-small
          class="btn-height-22 white--text"
          color="pink"
          @click="() => removeSlaves(selectedItems)"
          :disabled="selectedItems.length == 0"
        >
          Remove as slave
          <v-icon x-small right> fa-trash-o </v-icon>
        </v-btn>
      </template>
    </grid-toolbar>
    <v-data-table
      v-model="selectedItems"
      item-key="global_uuid"
      show-select
      :height="tableHeight"
      :loading="isFetching"
      :search="search"
      fixed-header
      :headers="headers"
      :items="dataItems"
      sort-by="calories"
      class="elevation-1"
      :custom-filter="filterCriteria"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:[`item.client_name`]="{ item }" class="pa-0">
        <span>
          <master-star-indicator :is-master="item.is_master" />
          <span>{{ item.client_name }}</span>
        </span>
      </template>
      <template v-slot:[`item.is_online`]="{ item }">
        <div v-if="item.is_online">
          <span class="font-weight-medium">
            <v-chip outlined color="nubeGreen">Online</v-chip>
          </span>
        </div>
        <div v-else>
          <span class="font-weight-medium">
            <v-chip outlined color="nubeYellow">Offline</v-chip>
          </span>
        </div>
      </template>
      <template v-slot:[`item.faliureRate`]="{ item }">
        <span v-if="item.faliureRate < 10" class="nubeGreen--text font-weight-bold">{{ item.faliureRate }}</span>
        <span v-if="item.faliureRate >= 10" class="orange--text font-weight-bold">{{ item.faliureRate }}</span>
      </template>
      <template v-slot:[`item.tags`]="{ item }">
        <v-chip-group multiple active-class="primary--text">
          <v-chip x-small v-for="tag in item.tags" :key="tag">
            {{ tag }}
          </v-chip>
        </v-chip-group>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <div>
          <tooltip-icon-button
            btnColor="blue"
            v-if="appSlaveId !== item.global_uuid || !appSlaveId"
            buttonSize="medium"
            btnIcon="fa-toggle-off"
            btnTooltip="Switch to slave mode"
            :onClick="() => switchToSlave(item)"
          />
          <tooltip-icon-button
            v-if="appSlaveId === item.global_uuid && appSlaveId"
            btnColor="orange"
            buttonSize="medium"
            btnIcon="fa-toggle-on"
            btnTooltip="Revert to master mode"
            :onClick="() => removeAppSlaveId()"
          />
          <tooltip-icon-button
            btnColor="red"
            btnIcon="fa-trash-o"
            btnTooltip="Remove as slave"
            :onClick="() => removeSlaves(item)"
            :loading="item.isDeleting"
          />
          <tooltip-icon-button
            btnColor="yellow"
            btnIcon="fa-comment-o"
            btnTooltip="Add Comment"
            :onClick="() => toggleDialog(dialogNames.comment, item)"
          />
          <tooltip-icon-button
            btnColor="green"
            btnIcon="fa-tag"
            btnTooltip="Add Tags"
            :onClick="() => toggleDialog(dialogNames.tags, item)"
          />
        </div>
      </template>
    </v-data-table>
    <v-dialog v-model="dialog[dialogNames.comment]" max-width="500px">
      <comment-form
        v-if="dialog[dialogNames.comment]"
        :onCancel="() => closeDialog(dialogNames.comment)"
        :onSave="comment => updateComment(comment)"
        :currentComment="selectedItem.comment"
      ></comment-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.tags]" max-width="500px">
      <tag-form
        v-if="dialog[dialogNames.tags]"
        :onCancel="() => closeDialog(dialogNames.tags)"
        :onSave="tags => updateTag(tags)"
        :currentTags="selectedItem.tags"
      ></tag-form>
    </v-dialog>
  </div>
</template>

<script>
import GridToolbar from '../Common/GridToolbar';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import MasterStarIndicator from '../Common/MasterStarIndicator.vue';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import GridItemsCounter from '../Common/gridItemsCounter.vue';
import HeaderSelector from '../HeaderSelector/headerSelector.vue';
import * as headerBuilderService from '../../services/headerBuilderService';
import * as gridTableHeadersService from '../../services/gridTableHeadersService';
import * as gridNames from '../../constants/gridNames';
import CommentForm from '../Common/CommentForm.vue';
import TagForm from '../Common/TagForm.vue';
import * as slaveDeviceService from '../../services/slaveDevicesService';

const MAX_VISIBLE_GRID_ITEMS = 20;

const dialogNames = {
  comment: 'comment',
  tags: 'tags',
};

export default {
  name: 'slave-devices',
  components: {
    GridToolbar,
    MasterStarIndicator,
    TooltipIconButton,
    GridItemsCounter,
    HeaderSelector,
    CommentForm,
    TagForm,
  },
  props: [
    'items',
    'removeSlaves',
    'isFetching',
    'switchToSlave',
    'removeAppSlaveId',
    'appSlaveId',
    'itemKeys',
    'refreshDevices',
  ],
  data: () => ({
    search: '',
    isSyncActive: false,
    searchItems: [],
    fileName: 'slave-devices',
    selectedItems: [],
    selectedItem: {},
    tableName: gridNames.SLAVE_APPS_GRID,
    headers: [],
    tableProps: { ...DATA_TABLE_PROPS },
    filters: {
      isOnline: false,
    },
    dialogNames: { ...dialogNames },
    dialog: {
      [dialogNames.comment]: false,
      [dialogNames.tags]: false,
    },
  }),
  mounted() {
    this.fetchSetGridHeaders();
  },
  computed: {
    dataItems() {
      return this.items
        .map((item, index) => ({
          ...item,
          sn: index + 1,
          faliureRate: ((item.failure_count / item.total_count) * 100).toFixed(2),
          client_name: item.is_master ? `   ${item.client_name}` : item.client_name,
        }))
        .filter(val => !(this.filters.isOnline && !val.is_online) && !(this.filters.isOffline && val.is_online));
    },
    appliedFilterCount() {
      return Object.keys(this.filters).reduce((acc, filterKey) => {
        if (this.filters[filterKey]) {
          acc++;
        }
        return acc;
      }, 0);
    },
    appliedFilterCount() {
      return Object.keys(this.filters).reduce((acc, filterKey) => {
        if (this.filters[filterKey]) {
          acc++;
        }
        return acc;
      }, 0);
    },
    totalCount() {
      return this.items.length;
    },
    onlineCount() {
      return this.items.reduce((acc, curr) => {
        if (curr.is_online) {
          acc = acc + 1;
        }
        return acc;
      }, 0);
    },
    offlineCount() {
      return this.items.reduce((acc, curr) => {
        if (!curr.is_online) {
          acc = acc + 1;
        }
        return acc;
      }, 0);
    },
    tableHeight() {
      return this.items && this.items.length > MAX_VISIBLE_GRID_ITEMS ? '75vh' : undefined;
    },
  },
  methods: {
    syncSlaveDevices() {
      this.isSyncActive = true;
      return slaveDeviceService
        .syncSlaveDevices()
        .then(() => {
          this.refreshDevices();
        })
        .finally(() => {
          this.isSyncActive = false;
        });
    },
    updateComment(comment) {
      const globalUUID = this.selectedItem.global_uuid;
      return slaveDeviceService.updateComment(globalUUID, { comment: comment }).then(data => {
        this.refreshDevices();
        return data;
      });
    },
    updateTag(tag) {
      const globalUUID = this.selectedItem.global_uuid;
      return slaveDeviceService.updateTags(globalUUID, { tags: tag }).then(data => {
        this.refreshDevices();
        return data;
      });
    },
    gridConfigChange() {
      this.fetchSetGridHeaders();
    },
    toggleDialog(dialogName, item) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    fetchSetGridHeaders() {
      gridTableHeadersService
        .fetch(gridNames.SLAVE_APPS_GRID)
        .then(gridConfig => {
          const gridHeaders = headerBuilderService.gridHeadersResolver(gridNames.SLAVE_APPS_GRID, gridConfig);
          this.headers = headerBuilderService.buildHeaders()(gridHeaders);
        })
        .catch(err => {
          this.headers = gridNames.DEFAULT_HEADERS[gridNames.SLAVE_APPS_GRID];
        });
    },
    formatDataService(data) {
      return data;
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      if (Array.isArray(value) && value.length > 0) {
        value = value.join();
      }
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        typeof value === 'string' &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
<style>
.v-data-table__wrapper::-webkit-scrollbar {
  width: 10px;
}

.v-data-table__wrapper::-webkit-scrollbar-track {
  background: #202020;
