<template>
  <v-card>
    <v-card-title>Tags Form</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validations>
        <v-row>
          <v-col cols="12">
            <chip-input v-model="tags" label="Tags" :onChipInputChange="onChipInputChange"></chip-input>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions class="text-right">
      <v-btn text @click="onCancel">Close</v-btn>
      <v-btn color="primary" :loading="isSaving" text @click="saveForm">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import ChipInput from './ChipInput.vue';
export default {
  name: 'tags-form',
  components: { ChipInput },
  props: ['onSave', 'currentTags', 'onCancel'],
  data() {
    return {
      isSaving: false,
      tags: [],
      searchValue: '',
      valid: false,
    };
  },
  mounted() {
    if (this.currentTags) {
      this.tags = this.currentTags;
    }
  },
  methods: {
    onChipInputChange(value) {
      this.searchValue = value;
    },
    onEnter(event) {
      if (event && event.keyCode == 13) {
        event.preventDefault();

        this.saveForm();
      }
    },
    onClose() {
      this.isCloseClicked = true;
      this.onCancel();
    },
    saveForm() {
      this.isSaving = true;
      if (this.searchValue) {
        this.tags.push(this.searchValue);
      }
      this.onSave(this.tags)
        .then(() => {
          this.$toasted.show('Successfully saved tags');
          this.onCancel();
        })
        .catch(err => {
          this.$toasted.show('Unsuccessful to save tags');
        })
        .finally(() => {
          this.isSaving = false;
        });
    },
  },
};
</script>
